.footer {
  height: 200px;
  background-image: url(../images/photos/footer-bg-Mobile.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media screen and (min-width: 744px) {
    height: 400px;
    background-image: url(../images/photos/footer-bg-Tablet.jpg);
  }

  @media screen and (min-width: 1260px) {
    height: 460px;
    background-image: url(../images/photos/footer-bg-Desktop.jpg);
  }
}
